@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  font-family: "Rubik", sans-serif;
  font-weight: 400;
  font-size: 14px;
  font-style: normal;
  background-color: #fcfcfc;
  color: #000000;
  height: 100vh;
}

h1,
h2,
h3,
p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

.sidebar {
  display: flex;
  flex-direction: row;
  align-items: stretch;
  min-height: 100vh;
  background-color: #003b72;
}

.sidebar__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-width: 190px;

  color: #ffffff;
  line-height: 17px;
}

.sidebar__logo {
  padding: 19px 20px;
  max-height: 55px;
}

.sidebar__btn {
  display: flex;
  justify-content: flex-start;
  margin-left: 19px;
  margin-right: 20px;
}

.sidebar__btn > .btn {
  width: 150px;
}

.sidebar__developer-logo {
  margin: 4px auto 16px 20px;
}

.menu-item {
  padding: 14px 20px 14px 18px;
  border-left: 2px solid transparent;
  background-color: #003b72;
}

.menu-item:hover {
  -webkit-filter: brightness(115%);
          filter: brightness(115%);
}

.menu__item {
  margin-bottom: 10px;
}

.menu-item__link {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.menu-item__icon {
  width: 18px;
  height: 18px;
}

.menu-item svg {
  fill: #ffffff;
}

.menu-item__text {
  margin-left: 14px;
}

.menu-item:active,
.menu-item--active {
  background-color: #fff;
  color: #7761ff;
  border-left: 2px solid #7761ff;
}

.menu-item:active svg,
.menu-item--active svg {
  fill: #7761ff;
}

.menu-item:active:hover,
.menu-item--active:hover {
  -webkit-filter: brightness(100%);
          filter: brightness(100%);
}

.btn {
  padding: 12px 15px;
  height: 40px;
  border: none;
  border-radius: 5px;
  background-color: #7761ff;
  color: #fff;
  font-family: "Rubik", sans-serif;
  font-size: 14px;
  font-weight: 500;
  text-align: center;
  vertical-align: middle;
}

.btn:hover {
  -webkit-filter: brightness(120%);
          filter: brightness(120%);
}

.btn:active {
  opacity: 70%;
}

.btn:focus {
  outline: none;
}

.header {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  flex: 0 0 55px;
  padding: 3px 40px;
  max-height: 49px;
  background-color: #50caff;
  color: #ffffff;
}

.header__title {
  font-size: 28px;
  line-height: 33px;
  font-weight: normal;
}

.header__wrapper {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.header__menu {
  display: flex;
}

.header__item {
  margin-right: 20px;
}

.header__user .avatar {
  margin-right: 8px;
  padding: 1px;
  background-color: transparent;
  border: 1px solid #ffffff;
  border-radius: 50%;
}

.avatar {
  border-radius: 50%;
}

.planned-visit-card {
  position: relative;
  padding: 20px;
  width: 431px;
  height: 130px;

  background: #ffffff;
  border: 2px solid #ffffff;
  box-shadow: 0px 0px 5px #ebe7ff;
  border-radius: 5px;
}

.planned-visit-card:hover {
  width: 431px;
  height: 130px;
  box-shadow: none;
  border: 2px solid #ebe7ff;
}

.planned-visit-card__wrapper {
  max-width: 280px;
}

.planned-visit-card__date {
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
}

.planned-visit-card__clinic {
  margin-top: 6px;
  font-weight: 300;
  line-height: 20px;
}

.planned-visit-card__doctor {
  margin-top: 9px;
}

.doctor {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.doctor__name {
  margin-bottom: 4px;
  font-weight: 500;
  line-height: 17px;
}

.doctor__department {
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  color: #ebe7ff;
}

.doctor__info {
  margin-left: 10px;
}

.planned-visit-card__btn {
  position: absolute;
  bottom: 20px;
  right: 20px;
}

.navigation-card {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: 536px;
  height: 179px;

  background: #ffffff;
  border: 2px solid #ffffff;
  box-shadow: 0px 0px 5px #ebe7ff;
  border-radius: 5px;
}

.navigation-card__icon-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 0 0 131px;
  height: 100%;
}

.navigation-card__icon svg {
  fill: #ebe7ff;
  stroke: #ffffff;
}

.navigation-card__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: 15px 34px 20px 36px;
}

.navigation-card__title {
  width: 100%;
  font-size: 28px;
  line-height: 33px;
  font-weight: normal;
}

.navigation-card__title::after {
  content: "";
  display: block;
  margin-top: 8px;
  width: 335;
  height: 0;
  border: 1px solid #ebe7ff;
}

.navigation-card__content {
  margin-top: 18px;
  max-width: 324px;
  font-size: 16px;
  line-height: 24px;
}

.navigation-card__content > ul > li {
  position: relative;
  margin-bottom: 6px;
  margin-left: 15px;
}

.navigation-card__content > ul > li:last-of-type {
  margin-bottom: 0px;
}

.navigation-card__content > ul > li::before {
  content: "";
  position: absolute;
  top: 10px;
  left: -15px;
  width: 4px;
  height: 4px;
  background-color: #ebe7ff;
  border-radius: 50%;
}

.navigation-card:active {
  opacity: 75%;
}

.navigation-card:hover {
  border-radius: 5px;
  box-shadow: none;
  border: 2px solid #50caff;
}

.navigation-card:hover .navigation-card__icon-wrapper {
  background-color: #50caff;
}

.navigation-card:hover .navigation-card__icon svg {
  fill: #ffffff;
  stroke: #50caff;
}

.navigation-card:hover .navigation-card__title::after {
  border: 1px solid #50caff;
}

.navigation-card:hover li::before {
  background-color: #50caff;
}

.nav-link {
  text-decoration: underline;
  color: #50caff;
}

.nav-link:hover {
  -webkit-filter: brightness(110%);
          filter: brightness(110%);
}

.nav-link:active {
  opacity: 70%;
}

.screen {
  display: flex;
  flex-direction: row;
  align-items: stretch;
}

.screen__wrapper {
  display: flex;
  width: 100%;
}

.screen__content {
  flex-direction: column;
}

.main {
  display: flex;
  flex: 1 0 auto;
  width: 100%;
}

.profile__container {
  width: 100%;
  padding-top: 15px;
  padding-right: 60px;
  padding-left: 20px;
}

.section-title {
  font-size: 16px;
  line-height: 19px;
}

.profile__section-title {
  margin-left: 20px;
  margin-bottom: 15px;
}

.scheduled-visits__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.scheduled-visits__card {
  margin-right: 20px;
  margin-bottom: 20px;
}

.scheduled-visits__all {
  padding-bottom: 20px;
  align-self: center;
  text-align: center;
}

.electronic-card {
  margin-top: 15px;
}

.electronic-card__row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-left: -8px;
  margin-right: -8px;
}

.electronic-card__col {
  padding-left: 8px;
  padding-right: 8px;
  margin-bottom: 15px;
}

.appointments__main {
  padding-top: 16px;
  padding-left: 18px;
}

.appointments__back {
  margin-left: 22px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
}

.appointments__section-title {
  margin-left: 14px;
  line-height: 18px;
}

.appointments__container {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
}

.my-appointments__wrapper {
  margin-top: 9px;
}

.my-appointments__content {
  padding: 2px 0 2px 2px;
  display: flex;
  flex-direction: column;
  width: 503px;
  height: 646px;
  overflow: scroll;
  scrollbar-color: #003b72 #ebe7ff;
  scrollbar-width: 8px;
}

::-webkit-scrollbar {
  width: 8px;
  height: 0px;
  background-color: #ebe7ff;
  border-radius: 5px;
}

/* ползунок скроллбара */
::-webkit-scrollbar-thumb {
  background-color: #003b72;
  border-radius: 5px;
}

.my-appointments__content > .planned-visit-card {
  margin-bottom: 15px;
}

.my-appointments__item:last-of-type {
  margin-bottom: 0px;
}

.appointments__calendar {
  margin-top: 10px;
  margin-left: 40px;
}

.visit-calendar {
  width: 573px;
  height: 481px;
  background-color: #ebe7ff;
  border-radius: 5px;
}

.visit-calendar__header {
  height: 57px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  background-color: #7761ff;
  color: #ffffff;
}

.visit-calendar__header span {
  margin: 0 38px;
}

.visit-calendar svg {
  fill: #fff;
}

.visit-calendar__body {
  padding: 0 10px;
}

.visit-calendar__week-days {
  display: flex;
  padding: 10px 0;
  font-size: 14px;
  line-height: 17px;
  font-weight: 300;
  text-align: center;
}

.week-days__day {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 79px;
}

.visit-calendar__days {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.calendar-day {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 61px;
  height: 42px;
  padding: 11px 10px 10px 8px;
}

.calendar-day--availible {
  background-color: #ffffff;
}

.calendar-day--not-availible {
  background-color: transparent;
}

.calendar-day__number {
  align-self: flex-start;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 25px;
  height: 20px;
  font-size: 20px;
  line-height: 20px;
  font-weight: normal;
}

.calendar-day--availible .calendar-day__number {
  color: #000000;
}

.calendar-day--not-availible .calendar-day__number {
  color: rgba(0, 0, 0, 0.25);
}

.calendar-day__visits {
  align-self: flex-end;
}

.calendar-visits {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;

  width: 22px;
  height: 22px;
  background-color: #50caff;
  border-radius: 50%;
  color: #ffffff;
  font-size: 14px;
  line-height: 100%;
  font-weight: normal;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

